$first-screen-bg-color: #25262C;
$black-color: #0E1014;
$blue-color: #4077F3;
$dark-blue-color: #3263d6;
$light-white-color: #E4E5EA;
$white-color: #fff;
$purple-color: #833AB4;
$yellow-color: #FCB045;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@mixin display($display, $jc) {
    display: $display;
    justify-content: $jc;
}
@mixin fonts($family, $weight) {
    font-family: $family;
    font-weight: $weight;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    color: $light-white-color;
    scroll-behavior: smooth;
}

body {
    background-color: $black-color;
}
// !NAVBAR
nav {
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0);
    .nav-container {
        @include fonts('Inter', 500);
        position: fixed;
        padding: 46px 20px 30px;
        z-index: 9;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: 100%;
        max-width: 1180px;
        min-height: 73px;
        color: $light-white-color;
        @include display(flex, space-between);
        align-items: center;
        transition: .3s;
        .logo {
            width: 100%;
            max-width: 150px;
            padding-right: 10px;
            position: relative;
            &::before {
                content: "крупный интегратор AmoCRM в Украине и ещё 8 странах";
                position: absolute;
                @include fonts('Montserrat', 300);
                width: 164px;
                min-height: 24px;
                display: block;
                font-size: 10px;
                line-height: 1.2;
                top: 100%;
                left: 0;
            }
        }
        .contacts {
            @include display(flex, space-between);
            width: 100%;
            max-width: 305px;
            .icons {
                width: 100%;
                max-width: 117px;
                @include display(flex, space-between);
            }
            span {
                display: flex;
                align-items: center;
                cursor: default;
                transition: .3s;
                &::selection {
                    background-color: $white-color;
                }
                &:hover {
                    color: $blue-color;
                }
            }
        }
        ul {
            list-style-type: none;
            @include display (flex, space-between);
            align-items: flex-start;
            width: 100%;
            max-width: 513px;
            padding-right: 10px;
            li {
                cursor: pointer;
                &:hover {
                    color: $white-color;
                }
            }
        }
    
    }
}
a {
    text-decoration: none;
    &:hover {
        svg {
            path {
                fill: $blue-color;
            }
        }
    }
    svg {
        max-width: 20px;
        width: 100%;
        path {
            transition: .3s;
        }
    }

}
.pulsating:hover {
    animation: pulsating 1.5s infinite;
}
@keyframes pulsating {
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}

// !FIRST SCREEN
@mixin before($width, $height) {
    content: "";
    position: absolute;
    background-position: center;
    background-size: contain;
    width: $width;
    height: $height;
}
.first-screen {
    height: 100vh;
    width: 100%;
    position: relative;
    &-content::before {
        content: "";
        background: inherit;
        width: 45%;
        height: 100vh;
    }
    &-bg {
        position: relative;
        width: 100%;
        height: 100%;
        img {
            max-width: 548px;
            position: absolute;
            left: 50%;
            top: 50%;
            width: 100%;
            height: auto;
            transform: translate(-50%,-50%);
            &::before {
                @include before(83px,83px);
                background-image: url(../img/first-screen/redBall.png);
            }
        }
    }
}

.lets-acquaintance {
    padding: 0 150px;
    width: 100%;
    h2 {
        line-height: 1.2;
        font-size: 48px;
        @include fonts('Inter', 400);
        margin-top: 50px;
        margin-bottom: 70px;
    }
    .content {
        @include display(flex, space-between);
        align-items: flex-end;
        .video {
            h3 {
                margin-bottom: 50px;
                max-width: 354px;
                font-size: 24px;
                @include fonts('Inter', 400);
            }
            &-player {
                @include display(flex, space-between);
                align-items: end;
                transition: .3s;
                &:hover {
                    a img {
                        opacity: 1;
                    }
                    button {
                        transform: scale(1.1);
                        transition: .3s;
                    }
                }
                a img {
                    width: 100%;
                    max-width: 457px;
                    opacity: .5;
                }
                button {
                    outline: 0;
                    border: 0;
                    cursor: pointer;
                    background-color: transparent;
                    border-radius: 75px;
                    border: 2px solid $white-color;
                    min-width: 44px;
                    height: 44px;
                    @include display(flex, center);
                    align-items: center;
                    margin: 0 150px 50px 28px;
                    position: relative;
                    &::after {
                        content: "Посмотрите видео и получите подарок";
                        @include fonts('Montserrat', 500);
                        text-align: left;
                        font-size: 16px;
                        display: block;
                        position: absolute;
                        width: 171px;
                        left: 0;
                        top: calc(100% + 10px);
                    }
                    svg {
                        width: 13px;
                    }
                }
            }
        }
        .founder {
            position: relative;
            z-index: 2;
            &::before {
                content: "Михаил Доброхвалов — основатель WelbeX.";
                @include fonts ('Inter', 300);
                font-size: 18px;
                width: 200px;
                text-align: left;
                position: absolute;
                left: -25%;
                top: 20px;
            }
            &::after {
                content: "";
                z-index: 1;
                position: absolute;
                right: 0;
                filter: blur(400px);
                width: 500px;
                opacity: .5;
                height: 500px;
                background-color: $purple-color;
                border-radius: 100%;
            }
            &-photo {
                img {
                    height: 375px;
                    width: 100%;
                    height: auto;
                }
            }
        }
    }
}

.text-gradient {
    color: #00000000;
    background-clip: text;
    background-image: linear-gradient(92deg, #FCB045 27.14%, #FF3F78 121.36%);
}